<template>
    <section class="search">
        <h1>Track Player Stats</h1>
        <form v-on:submit.prevent="onSubmit">
            <div class="form-group">
                <label for="steamid">Steam Profile</label>
                <input type="text" name="text" v-model="steamid" id="steamid" placeholder="Steam ID, Steam Community URL, Steam Vanity Username">
            </div>
            <div class="form-group">
                <input type="submit" value="Submit" class="btn">
            </div>
        </form>
    </section>
</template>

<script>
export default {
    name: 'Search',
    data() {
        return {
            steamid: ""
        }
    },
    beforeCreate() {
        document.body.className = "body-bg-image"
    },
    methods: {
        onSubmit(){
            if(!this.steamid){
                this.$toast.error(`Please enter a steamid`, {position: 'top', duration: 4000});
            } else {
                this.$router.push(`/profile/steam/${this.steamid}`)
            }
        }
    },
}
</script>

